<template>
    <div>
        <h1>扫描可用IP地址</h1>
        <el-alert title="除非某种极端情况出现，否则尽量不要去扫描/8、/12、/16等跨度较大的网段；（考虑到扫描速度，底层常态化开启了100个线程待命）其扫描时间已经比普通情况缩短200倍了，如果满功率长时间扫描大网段，您的电脑会陷入严重卡顿～" type="info" center></el-alert>

        <br>

        <!--要提交的表单-->
        <el-form ref="form" :model="form" :rules="rules" label-width="120px">
            <el-row>
                <el-col :span="8" :offset="7">
                    <el-form-item prop="cidr" label="IPv4网段:" :rules='[
                        { required: true, message: "内容不能为空（填写格式：192.168.0.1/24）", trigger: "blur" },
                        { pattern: "^(?:(?:[0-9]|[1-9][0-9]|1[0-9]{2}|2[0-4][0-9]|25[0-5])\\.){3}(?:[0-9]|[1-9][0-9]|1[0-9]{2}|2[0-4][0-9]|25[0-5])\\/([1-9]|[1-2]\\d|3[0-2])$",message:"填写格式为：192.168.0.1/24（格式正确后，本提示自动消失...）"}
                        ]'>
                        <el-input v-model="form.cidr" placeholder="192.168.0.1/24"></el-input>
                    </el-form-item>
                </el-col>

                <el-col :span="2">
                    <el-button type="primary" style="float: left;margin-left: 5px" @click="getProbeResult">扫描</el-button>
                </el-col>
            </el-row>
        </el-form>

        <!--分割线-->
        <el-divider><span style="color: cadetblue;font-size: 10px">idle resource</span></el-divider>

        <!--结果显示表格-->
        <el-table :data="ip_list" border v-loading="loading" element-loading-text="扫描中...." element-loading-spinner="el-icon-loading" style="width: 100%">
            <el-table-column prop="ip" label="空闲IP地址"></el-table-column>
        </el-table>
    </div>
</template>

<script>
export default {
    name: "cc-ip-probe",
    data() {
        return {
            form: {
                cidr: "",
            },
            rules: {
                cidr: [
                    {required: true, message: "网段不能为空", trigger: 'blur'},
                    {min: 3, message: "内容不能少于3个字符"}
                ]
            },
            loading: false,
            ip_list: [
                // {"ip": "172.30.10.1"},
                // {"ip": "172.30.10.2"},
                // {"ip": "172.30.10.3"},
                // {"ip": "172.30.10.4"},
                // {"ip": "172.30.10.5"},
                // {"ip": "172.30.10.6"},
                // {"ip": "172.30.10.7"},
                // {"ip": "172.30.10.8"},
            ]
        }
    },
    methods: {
        getProbeResult: function () {
            if (this.form.cidr.length !== 0) {
                this.loading = true
                this.$axios({
                    url: "/api/v1/probe-ip",
                    method: "post",
                    headers: {
                        'Content-Type': 'application/json'
                    },
                    data: {
                        "cidr": this.form.cidr
                    }
                }).then(response => {
                    this.ip_list = response.data
                    this.loading = false
                })
            } else {
                this.$message('扫描"网段"不能为空...');
            }
        }
    }
}
</script>

